





























import { Component, Vue  } from 'vue-property-decorator';
import api from '../api/network';
import store from '../modules/store';
import Global from '../modules/Global';
import swiper from '../modules/swiper';
import DropDownRefresh from '../modules/refresh';

@Component({})
export default class Plug extends Vue {
    private swiperList = store.swiperList;

    private swipers = [
        {
            id: 'swiper-1',
            tip: '没有回路和底部圆点',
        }, {
            id: 'swiper-2',
            tip: '有回路和底部圆点'
        }, {
            id: 'swiper-3',
            tip: '垂直滚动'
        }, 
    ];
    
    private content = '';

    showAlert() {
        Global.openDialog({
            type: 'alert',
            content: '提示框',
        });
    }

    showConfirm() {
        Global.openDialog({
            type: 'confirm',
            content: '确定-取消提示框'
        }, () => {}, () => {
            Global.openDialog({
                type: 'alert',
                content: '点击了取消',
            });
        });
    }

    showToast() {
        Global.showToast('提示条~');
    }

    showLoading() {
        Global.openDialog({
            type: 'loading'
        });
        setTimeout(function() {
            Global.hideDialog();
        }, 3000);
    }

    postData() {
        Global.openDialog({
            type: 'loading'
        });
        api.testPost(res => {
            Global.hideDialog();
            this.content = JSON.stringify(res);
        }, err => {
            Global.hideDialog();
        });
    }
    
    copyText() {
        const el: any = this.$refs['text-box'];
        el.select();
        el.setSelectionRange(0, el.value.length);
        document.execCommand('copy');
        Global.openDialog({
            type: 'confirm',
            content: '复制成功，是否清空文本框数据？',
        }, () => {
            this.content = '';
        });
    }

    mounted() {
        swiper({
            el: '#' + this.swipers[0].id,
            autoPaly: true
        });
        swiper({
            el: '#' + this.swipers[1].id,
            autoPaly: true,
            pagination: true,
            loop: true,
            interval: 4000
        });
        swiper({
            el: '#' + this.swipers[2].id,
            autoPaly: true,
            pagination: true,
            loop: true,
            vertical: true
        });
        const dr = new DropDownRefresh('.plug');
        dr.onStart(100, function() {
            // console.log('下拉成功');
            Global.openDialog({
                type: 'loading'
            });
            setTimeout(function() {
                Global.hideDialog();
                dr.end();
            }, 2000);
        })
    }
}
